






















































































































































































































#areaManagement-wrapper{
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .radioBox {
    padding: 0 15px 15px;
    text-align: left;
  }
  .showBox {
    padding: 0 15px;
    height: calc(100% - 32px);
    position: relative;
    .sta_box {
      position: absolute;
      width: 49%;
      overflow: hidden;
      height: 100%;
      padding: 15px;
      background: #fff;
      &.sta_box1 {
        left: 15px;
      }
      &.sta_box2 {
        right: 15px;
      }
      .con_box{
        height: 100%;
        width: 100%;
        border-radius: 10px;
        box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
        position: relative;
        .ivu-table-wrapper {
          border-radius: 10px;
        }
        .boxBtn{
          position: absolute;
          right: 10px;
          top:10px;
          z-index: 80;
        }
        .bigBox{
          width:100%;
          height:100%;
          .smallBox{
            width:100%;
            height:100%;
          }
        }
        .smallToBig {
          .smallBox {
            width: 100%;
          }
        }
      }
    }
  }
  .page_box{
    width: 100%;
    padding: 15px 15px 0;
    text-align: right
  }
  /* box放大 */
  .activeBox{
    width: calc(100% - 15px) !important;
    transition: all 0.4s ease;
    z-index: 999;
  }
  .ivu-radio-group {
    .ivu-radio-wrapper {
      margin-left: 10px;
      border-radius: 4px;
      &:first-child {
        margin-left: 0;
      }
      &.ivu-radio-wrapper-checked {
        background: #2d8cf0;
        color: #fff;
      }
    }
  }
}
